import React from 'react'
import NavList from './NavList'
import Logo from '../../sources/images/logo.png'
const NavBar = () => {
  return (
   
    <nav className="fixed w-full z-30 top-0 bg-gray-900 border-gray-200 px-2 sm:px-4 py-2.5 md:rounded-b-xl ">
      <div className="m-3 flex flex-wrap justify-between items-center">
        <div className="flex items-center">
          <img src={Logo} className="mr-3 h-9 lg:h-16" alt="Flowbite Logo" />
          <NavList/>
        </div>
      </div>
    </nav>
    
    
  )
}

export default NavBar