import React from 'react'
import { useState } from 'react'
import {derivative, evaluate, compile} from 'mathjs'
const Home = () => {
  const [expInputValue, setExpInputValue] = useState('sin(x)')
  const [expValue, setExpValue] = useState('')
  const [xInputValue, setXinputValue] = useState('5')
  const [xValue, setXValue] = useState('')
  const [cdValue, setcdValue] = useState([])
  const [fdValue, setfdValue] = useState([])
  const [bdValue, setbdValue] = useState([])
  const handleChangeExp = (e) => {
    setExpInputValue(e.target.value)
  }
  const handleChangeVar = (e) => {
    setXinputValue(e.target.value)
  }
  
  const numerical_diff = (input,x) => {
    const eq = compile(input);
    x = Number.parseFloat(x);
    const init_delta = 0.001;
    const dx = derivative(input, 'x');
    const dx_result = dx.evaluate( {x: x});
    
    const cd_result = [];
    const fd_result = [];
    const bd_result = [];

    let ans = 0;
    let iter = 1;
    let delta = init_delta;

    do{
        ans = ( eq.evaluate( {x : x + delta  } ) - eq.evaluate( {x : x - delta  } ) ) / (2 * delta);
        cd_result.push( {
            'n': iter++,
            'ans': Number.parseFloat(ans).toFixed(8),
            'delta': delta
        })
        delta /= 2;
    }while( Number.parseFloat(dx_result).toFixed(5) !== Number.parseFloat(ans).toFixed(5) )

    ans = 0;
    iter = 1;
    delta = init_delta;
    
    do{
        ans = ( eq.evaluate( {x : x + delta  } ) - eq.evaluate( {x : x } ) ) / (delta);
        fd_result.push( {
            'n': iter++,
            'ans': Number.parseFloat(ans).toFixed(8),
            'delta': delta
        })
        delta /= 2;
    }while( Number.parseFloat(dx_result).toFixed(5) !== Number.parseFloat(ans).toFixed(5) )

    ans = 0;
    iter = 1;
    delta = init_delta;

    do{
        ans = ( eq.evaluate( {x : x } ) - eq.evaluate( {x : x - delta } ) ) / (delta);
        bd_result.push( {
            'n': iter++,
            'ans': Number.parseFloat(ans).toFixed(8),
            'delta': delta
        })
        delta /= 2;
    }while( Number.parseFloat(dx_result).toFixed(5) !== Number.parseFloat(ans).toFixed(5) )
    showDerivative()
    console.log("Central Difference" ,cd_result);
    console.log("Forward Difference",fd_result);
    console.log("Backward Difference", bd_result);

    setcdValue(cd_result)
    setfdValue(fd_result)
    setbdValue(bd_result)
  }

  const showDerivative = () => {
    try{
      const eval_ = derivative(expInputValue, 'x')
      setExpValue(derivative(expInputValue, 'x').toString())
      setXValue(eval_.evaluate({x: xInputValue}))
      
    }catch(err){
      setExpValue('Invalid Expression')
    }
  }
  return (
    <div className="bg-gray-600 items-center" id="home">
      <div className="container mx-auto flex lg:flex-row justify-center">
      <div className=" pt-64 w-1/2 z-10 flex flex-col text-white ">
          <h1 className="mb-4 text-3xl font-bold">Introduction</h1>
          <p className="text-justify">Welcome to our Numerical Difference Calculator! This React application allows users to quickly calculate the numerical difference based on the inputted function and the inputted x value. It features a user-friendly interface, input validation, real-time calculation, dynamic output, responsive design, and easy integration. Enjoy using our calculator and feel free to provide feedback or ask questions. Happy calculating!</p>
      </div>
      </div>
      <div className="container mx-auto flex lg:flex-row justify-center">
        <div className="pt-24 w-1/2 z-10 flex flex-col text-white ">
          <h1 className="mb-4 text-3xl font-bold">Program</h1>
          <label className="text-2xl ">Enter Expression</label>
          <input className="text-black px-2" type="text" value={expInputValue} onChange={handleChangeExp} />
          <label className="text-2xl">Enter x</label>
          <input className="text-black px-2" type="text" value={xInputValue} onChange={handleChangeVar} />
          <button className="bg-blue-700 p-2 my-4 hover:bg-blue-900 text-white font-bold py-2 px-4 rounded w-3/4" onClick={() => numerical_diff(expInputValue,xInputValue)}>Enter</button>
          <label className="mt-8 text-xl font-bold">Derivative Expression: </label>
          <h1>{expValue}</h1>
          <label className="mt-8 text-xl font-bold">Evaluated expression:</label>
          <h1>{xValue}</h1>
          <h1 className="mt-8 text-xl font-bold">Forward Difference</h1>
          <table className="p-12 border border-collapse ">
            <thead>
              <tr className="p-2 ">
                <th className="p-2 border">n</th>
                <th className="p-2 border">Delta_x</th>
                <th className="p-2 border">Answer</th>
              </tr>
            </thead>
            <tbody>
              {fdValue.map((item) => (
                <tr key={item.n}>
                  <td className="p-2 border">{item.n}</td>
                  <td className="p-2 border">{item.delta}</td>
                  <td className="p-2 border">{item.ans}</td>
                </tr>
              ))}
            </tbody>
          </table>
          <h1 className="mt-8 text-xl font-bold">Backward Difference</h1>
          <table className="p-12 border border-collapse ">
            <thead>
              <tr className="p-2 ">
                <th className="p-2 border">n</th>
                <th className="p-2 border">Delta_x</th>
                <th className="p-2 border">Answer</th>
              </tr>
            </thead>
            <tbody>
              {bdValue.map((item) => (
                <tr key={item.n}>
                  <td className="p-2 border">{item.n}</td>
                  <td className="p-2 border">{item.delta}</td>
                  <td className="p-2 border">{item.ans}</td>
                </tr>
              ))}
            </tbody>
          </table>
          <h1 className="mt-8 text-xl font-bold">Central Difference</h1>
          <table className="p-12 border border-collapse ">
            <thead>
              <tr className="p-2 ">
                <th className="p-2 border">n</th>
                <th className="p-2 border">Delta_x</th>
                <th className="p-2 border">Answer</th>
              </tr>
            </thead>
            <tbody>
              {cdValue.map((item) => (
                <tr key={item.n}>
                  <td className="p-2 border">{item.n}</td>
                  <td className="p-2 border">{item.delta}</td>
                  <td className="p-2 border">{item.ans}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
      <div className="container mx-auto flex lg:flex-row justify-center">
        <div className=" pt-24 w-1/2 z-10 flex flex-col text-white ">
          <div class="container mx-auto p-4">
            <h1 class="text-2xl font-bold mb-4">Specifications</h1>
            <ul class="list-disc list-inside">
              <li class="mb-2">User Interface: Clean and intuitive interface with input fields and prominent display of the calculated difference.</li>
              <li class="mb-2">Input Validation: Validate user inputs for numerical values, display error messages for invalid inputs.</li>
              <li class="mb-2">Real-Time Calculation: Automatically calculate the difference between values without manual initiation.</li>
              <li class="mb-2">Dynamic Output: Display the calculated difference in a visually appealing and easy-to-read format.</li>
              <li class="mb-2">Responsive Design: Ensure compatibility and optimal user experience across devices and screen sizes.</li>
              <li class="mb-2">Easy Integration: Built with React for seamless integration into existing projects.</li>
              <li class="mb-2">Error Handling: Handle errors gracefully and provide clear instructions for issue resolution.</li>
              <li class="mb-2">Limitations:</li>
              <ul class="list-disc list-inside ml-4">
                <li>Error value is five decimal places maximum only.</li>
                <li>Delta is set and constant at 0.001.</li>
                <li>Unable to calculate ln() function.</li>
                <li>X is the only constant variable available.</li>
              </ul>
            </ul>
          </div>
        </div>
      </div>
      <div className="pt-20 pb-24 container mx-auto flex lg:flex-row justify-center">
      <div className=" w-1/2 z-10 flex flex-col text-white ">
          <h1 className="mb-4 text-3xl font-bold">Conclusion</h1>
          <p className="text-justify">Our Numerical Difference Calculator is a reliable and user-friendly React application that simplifies the process of calculating numerical differences of the inputted function and value. With real-time calculation, input validation, dynamic output, and responsive design, it offers a seamless and efficient experience. We are excited to provide this valuable tool and welcome any feedback or inquiries. Thank you for choosing our Numerical Difference Calculator!</p>
      </div>
      </div>
    </div>
  )
}

export default Home